@font-face {
  font-family: 'SF_Pro_Display';
  src: url('fonts/Display/SFProDisplay-Regular.eot');
  src: url('fonts/Display/SFProDisplay-Regular.eot?#iefix') format('embedded-opentype'),
    url('fonts/Display/SFProDisplay-Regular.woff2') format('woff2'),
    url('fonts/Display/SFProDisplay-Regular.woff') format('woff'),
    url('fonts/Display/SFProDisplay-Regular.ttf') format('truetype'),
    url('fonts/Display/SFProDisplay-Regular.svg#SFProDisplay-Regular') format('svg');
  font-weight: normal;
  font-style: normal;
}

